.project-goal {
  padding-bottom: 120px;

  .grid {
    @include small-tablet {
      display: block;
    }
  }

  &__left {
    grid-column: 6 span;
    padding-top: 24px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
    line-height: 1.5;
    color: var(--gray);
    margin-bottom: 27px;
  }

  &__right {
    grid-column: 6 span;
    display: flex;
    justify-content: flex-end;

    @include small-tablet {
      justify-content: flex-start;
    }
  }

  &__title {
    margin-bottom: 41px;
  }

  @include mobile {
    padding-bottom: 70px;
  }
}
.project-info {
  width: 495px;
  background: var(--white);
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  border-radius: 4px;
  padding: 40px 60px;

  &__item {
    display: flex;
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }

  &__category {
    flex-shrink: 0;
    margin: 0;
    text-transform: uppercase;
    width: 130px;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);
  }

  &__value {
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
  }

  @include small-tablet {
    width: 100%;
  }

  @include mobile {
    padding: 10px 0;
  }
}
