.related-projects {
  padding-top: 180px;
  background-color: var(--white);

  &__container {
    max-width: 1260px;
    margin: 0 auto;
  }

  &__items {
    background-color: var(--white);
    padding: 0 15px;
    padding-bottom: 15px;
    &.swiper-slide {
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    &.swiper-visible {
      opacity: 1;
      visibility: visible;
    }
  }

  &__slider-nav {
    transform: translateX(12px);

    @include mobile {
      transform: none;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 46px;

    @include mobile {
      display: block;
      margin-bottom: 25px;
    }
  }

  &__title {
    padding-bottom: 12px;
    max-width: 710px;
    font-weight: bold;
    font-size: 46px;
    line-height: 1.3;
    color: var(--black);
    margin: 0;

    @include small-tablet {
      font-size: 35px;
    }
  }

  &__learn {
    padding-top: 31.5px;
    padding-bottom: 68px;

    @include small-tablet {
      margin: 0 15px;
    }
  }

  @include mobile {
    padding-top: 70px;
  }
}

.related-projects-item {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  background: var(--white);
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  border-radius: 4px;
  transition: all 0.3s ease-in-out;

  &:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }

  &:hover {
    transform: translateY(3px);
  }

  &__content {
    padding: 15px 20px 24px;
  }

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 4px;

    @include mini-desktop {
      font-size: 17px;
    }

    @include small-tablet {
      min-height: 52px;
    }
  }

  &__subtitle {
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
  }

  &__btn {
    margin-top: 24px;
    cursor: pointer;
    font-weight: bold;
    font-size: 14px;
    display: none;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--primary);
    border: 1px solid currentColor;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 13px 31px;
  }
}
