.portfolio {
  padding-bottom: 100px;
}
.portfolio-tabs-nav {
  margin-bottom: 57px;
  display: flex;
  align-items: center;
  --gap: 20px;
  transform: translateY(-60px);

  &__item {
    width: calc((100% - (var(--gap) * 4)) / 5);
    &:not(:last-child) {
      margin-right: var(--gap);
    }

    @include small-tablet {
      margin-bottom: 20px;
      width: calc((100% - (var(--gap) * 2)) / 3);
    }

    @include mobile {
      margin-right: 0 !important;
      width: 100%;
    }
  }

  &__text {
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    text-align: center;
    color: #9a9ca5;
    transition: color ease-in-out 0.3s;
    pointer-events: none;
  }

  &__btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 154px;
    width: 100%;
    background: var(--white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;
    transition: background-color ease-in-out 0.3s;

    &--active {
      background-color: var(--primary);
      pointer-events: none;

      .portfolio-tabs-nav__text {
        color: #fff !important;
      }
      .portfolio-tabs-nav__icon {
        fill: var(--white);
      }
    }
  }

  &__icon {
    display: block;
    width: 48px;
    height: 48px;
    object-fit: cover;
    margin-bottom: 16px;
    fill: var(--primary);
    pointer-events: none;
  }

  @include small-tablet {
    flex-wrap: wrap;
    justify-content: center;
    transform: none;
    margin-top: 30px;
  }
}
.portfolio-tabs {
  &__item {
    display: none;
    grid-column: 4 span;

    &--visible {
      display: block;
    }

    &--visible-more {
      display: block !important;
    }
  }
}
.portfolio-more {
  margin-top: 55px;
  display: inline-flex;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.6;
  color: var(--gray);
  transition: all 0.3s ease-in;

  svg {
    width: 24px;
    height: 24px;
    color: inherit;
    fill: currentColor;
    object-fit: cover;
    margin-right: 16px;
  }

  &:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }

  &:hover {
    color: var(--primary);
  }

  &:active {
    color: var(--active-color);
  }
}

.portfolio-tabs-nav__btn {
  &:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }

  &:hover {
    .portfolio-tabs-nav__text {
      color: var(--black);
    }
  }
}
