.news-article {
  overflow: hidden;
  height: 100%;
  background: var(--white);
  box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
    0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
  border-radius: 4px;

  &--sm {
    .news-article__descr {
      display: none;
    }

    .news-article__text {
      padding: 16px 24px;
    }
  }

  &__text {
    padding: 24px;
  }

  &__img {
    @media (max-width: 1024px) {
      width: 100%;

      & img {
        width: 100%;
        height: auto;
        object-fit: cover;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 13px;
  }

  &__link {
    color: var(--black);
    font-weight: bold;
    font-size: 20px;
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__meta {
    margin-bottom: 17px;
  }

  &__descr {
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    margin: 0;
  }
}

.article-meta {
  font-size: 14px;
  line-height: 1.5;
  align-items: center;
  color: var(--storm);

  &__item {
    color: inherit;
    position: relative;

    &:not(:last-child) {
      margin-right: 21px;

      &::after {
        content: "";
        position: absolute;
        right: -13px;
        top: 2px;
        width: 1px;
        height: 11px;
        color: inherit;
        background-color: currentColor;
      }
    }

    &--comments {
      padding-left: 24px;
      background-image: url("../img/recent-news-chat.svg");
      background-repeat: no-repeat;
      background-size: 16px 16px;
      background-position: left center;
    }
  }
}

a.article-meta__item {
  transition: all 0.3s ease-in-out;

  &:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }

  &:hover {
    color: var(--primary);
  }

  &:active {
    color: var(--active-color);
  }
}
