.footer {
  position: relative;
  padding: 80px 0;
  background-color: var(--black);
  color: rgba(#fff, 0.6);

  &__logo {
    margin-right: 60px;

    @include mobile {
      display: inline-block;
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  &__left {
    grid-column: 7 span;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    margin-bottom: 22px;

    @include mobile {
      display: block;
    }
  }

  &__form {
    grid-column: 5 span;

    @include small-tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__adress {
    grid-column: 4 span;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__nav {
    grid-column: 8 span;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &-title {
    margin: 0;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--white);
    margin-bottom: 15px;
  }

  &__descr {
    margin: 0;
    width: 100%;
    max-width: 495px;
    display: block;
    font-size: 14px;
    line-height: 1.5;
  }

  &__copyright {
    grid-column: 12 span;
    font-size: 12px;
    line-height: 1.5;
    color: var(--white);
    padding-top: 30px;

    & span {
      padding-left: 18px;
      background-image: url("../img/svg/heart.svg");
      background-repeat: no-repeat;
      background-size: 16px 16px;
      background-position: left center;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  @include mobile {
    padding: 50px 0;
  }
}
.footer-form {
  &__subtitle {
    margin: 0;
    font-weight: bold;
    font-size: 24px;
    line-height: 1.5;
    color: var(--white);
    margin-bottom: 24px;
  }

  &__items {
    display: flex;
    align-items: center;
  }

  &__btn {
    padding: 12px 19px;
    border-radius: 0px 4px 4px 0px;
  }

  &__desc {
    margin: 0;
    font-size: 12px;
    line-height: 1.5;
    margin-top: 16px;
  }

  & .form-field {
    margin: 0;
    max-width: 364px;
    width: 100%;
    height: 44px;
  }

  & .form-field__input {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    background: rgba(255, 255, 255, 0.12);
    border: 1px solid rgba(255, 255, 255, 0.2);
  }
}
.footer-adress {
  padding-top: 20px;

  &__item {
    display: block;
    font-size: 16px;
    line-height: 1.6;

    &:not(:last-child) {
      margin-bottom: 4px;
    }

    & a {
      color: inherit;
      transition: all 0.3s ease-in-out;

      &:focus {
        outline: 2px solid var(--primary);
        outline-offset: 2px;
      }

      &:hover {
        color: var(--white);
      }

      &:active {
        color: var(--white);
      }
    }
  }

  &__name {
    font-size: 16px;
    line-height: 1.6;
    color: var(--white);
    margin-right: 4px;
  }
}
.footer-nav {
  display: flex;
  padding-top: 20px;
  padding-left: 54px;
  &__col {
    &:not(:last-child) {
      margin-right: 125px;

      @include small-tablet {
        margin-right: 65px;
      }

      @include mobile {
        margin-right: 35px;
      }
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__link {
    font-size: 16px;
    line-height: 1.6;
    color: inherit;
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--white);
    }

    &:active {
      color: var(--white);
    }
  }

  @include small-tablet {
    padding-left: 24px;
  }

  @include mobile {
    padding-left: 0;
  }
}

.to-top {
  position: absolute;
  bottom: 80px;
  right: calc((100% - 1230px) / 2) !important;
  z-index: 1000;
  display: flex;
  align-items: flex-end;

  @media (max-width: 1263px) {
    display: none;
  }

  &__text {
    text-transform: uppercase;
    font-size: 14px;
    line-height: 1.5;
    color: var(--white);
    margin-right: 16px;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    background-color: var(--primary);
    border-radius: 4px;
    transition: all 0.3s ease-in-out;

    svg {
      fill: var(--white);
      width: 24px;
      height: 24px;
      object-fit: cover;
    }

    &:hover {
      background-color: var(--active-color);
    }
  }
}
