.contacts-info {
  padding: 60px 0 118px;
  background-color: var(--athens);

  .descr {
    margin-bottom: 60px;
  }

  &__list {
    --contacts-gap: 120px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--contacts-gap) * 2);
    margin-bottom: 120px;

    @include mini-desktop {
      --contacts-gap: 40px;
    }

    @include small-tablet {
      display: block;
    }
  }

  &__soc {
    margin-bottom: 40px;
  }

  &__social {
    justify-content: center;
    margin-top: 20px;

    .social__item {
      &:not(:last-child) {
        margin-right: 24px;
      }
    }

    .social__link {
      width: 32px;
      height: 32px;

      svg {
        fill: var(--storm);
      }
    }
  }

  &__item {
    position: relative;

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      right: calc(-1 * var(--contacts-gap));
      top: 0;
      width: 1px;
      height: 100%;
      background-image: url("../img/mission-line.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    @media (max-width: 768px) {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 24px;
  }

  &__address {
    display: inline-block;
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);
    margin-bottom: 8px;
  }

  &__link {
    display: inline-block;
    margin: 0;
    font-size: 16px;
    border-bottom: 1px solid var(--primary);
    color: var(--primary);
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
      border-bottom: 1px solid transparent;
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__bottom {
    margin-top: 25px;
  }

  &__element {
    display: inline-block;
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);

    &:not(:last-child) {
      margin-bottom: 5px;
    }

    span {
      padding-right: 5px;
    }
  }

  &__value {
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);
    margin-right: 4px;
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
      border-bottom: 1px solid transparent;
    }

    &:active {
      color: var(--active-color);
    }
  }
}
