.employee-benefits {
  padding-bottom: 153px;
  background-color: var(--white);

  &__list {
    --mission-gap: 94px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: calc(var(--mission-gap) * 2);
    margin-bottom: 30px;

    @include tablet {
      --mission-gap: 30px;
    }

    @include small-tablet {
      --mission-gap: 10px;
    }

    @include mobile {
      display: block;
    }
  }

  &__item {
    text-align: center;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: 48px 48px;
    padding-top: 74px;
    position: relative;

    &:not(:last-child)::after {
      content: "";
      position: absolute;
      right: calc(-1 * var(--mission-gap));
      top: 0;
      width: 1px;
      height: 100%;
      background-image: url("../img/mission-line.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    @include mobile {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__item-title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 8px;
  }

  &__item-text {
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
  }

  &__item--training {
    background-image: url("../img/brain.svg");
  }

  &__item--growth {
    background-image: url("../img/success.svg");
  }

  &__item--salary {
    background-image: url("../img/budget.svg");
  }

  @include mobile {
    padding-bottom: 70px;
  }
}
