.achieve {
  padding-bottom: 120px;
  background-image: url("../img/achieve-bg.png");
  background-repeat: no-repeat;
  background-position: right bottom -300px;
  &__list {
    margin-bottom: 240px;

    @include mobile {
      margin-bottom: 50px;
    }
  }

  &__item {
    grid-column: 3 span;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 66px;
    background-repeat: no-repeat;
    background-size: 48px 48px;
    background-position: top center;

    &:not(:last-child) {
      margin-right: 46px;
    }

    @include tablet {
      grid-column: 6 span;
      &:not(:last-child) {
        margin-right: 0px;
      }
    }
  }

  &__value {
    font-weight: bold;
    font-size: 46px;
    line-height: 1.3;
    color: var(--primary);
    margin-bottom: 8px;

    @media (max-width: 1240px) {
      font-size: 36px;
    }
  }

  &__img {
    @media (max-width: 1200px) {
      max-width: 45%;
    }

    @include tablet {
      max-width: 100%;
    }
  }

  &__text {
    margin: 0;
    text-align: center;
    font-size: 14px;
    line-height: 1.5;
    color: var(--black);
    max-width: 240px;
  }

  &__item--reccom {
    background-image: url("../img/likeup.svg");
  }

  &__item--renov {
    background-image: url("../img/painting.svg");
  }

  &__item--spec {
    background-image: url("../img/helmet.svg");
  }

  &__item--finish {
    background-image: url("../img/paintbrush.svg");
  }

  &__content {
    display: flex;
    align-items: flex-start;

    @include tablet {
      display: block;
    }
  }

  &__info {
    position: relative;
    top: -150px;
    left: -30px;
    z-index: 10;
    padding: 80px 80px;
    padding-right: 40px;
    padding-bottom: 60px;
    flex-shrink: 0;
    width: 735px;
    background: var(--white);
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;

    @media (max-width: 1200px) {
      width: 55%;
      left: 0;
      top: 0;
    }

    @include tablet {
      width: 100%;
    }

    @include mobile {
      padding: 0px;
    }
  }
}

.quote {
  margin: 0;
  &__content {
    position: relative;
    margin-bottom: 22px;
    padding: 9px 53px;

    &::before {
      content: "";
      position: absolute;
      left: -3px;
      top: 28px;
      width: 28px;
      height: 20px;
      background-image: url("../img/braces.svg");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include mobile {
        left: 0;
      }
    }
    p {
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      color: var(--black);
    }

    @include mobile {
      padding: 0;
      padding-top: 40px;
      padding-bottom: 20px;
    }
  }

  &__footer {
    padding: 20px 53px;
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
    background-image: url("../img/signature.svg");
    background-position: right 65px center;
    background-repeat: no-repeat;

    @include mobile {
      background-position: bottom 30px center;
      padding-bottom: 120px;
    }
  }

  &__author {
    display: block;
    margin-bottom: 2px;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);
  }
}
