.facts {
  position: relative;
  padding-top: 180px;
  padding-bottom: 60px;
  background-color: var(--white);
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: -195px;
    left: -9px;
    width: 570px;
    height: 1065px;
    background-image: url("../img/facts-bg.png");
    background-position: center;
    background-repeat: no-repeat;
  }

  &__list {
    max-width: 736px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 48px;
    margin-top: 60px;

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      display: block;
    }
  }

  &__item {
    @include mobile {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  @include mobile {
    padding: 40px 0;
  }
}
.facts-element {
  &__circle {
    position: relative;
    width: 148px;
    height: 148px;
    margin-bottom: 20px;

    --progress-stroke: #000;

    & svg {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    .progress {
      stroke: var(--progress-stroke);
      transform: rotate(-90deg) translateX(20px);
      transform-origin: center;
    }

    &--one-color {
      --progress-stroke: #f89828;
    }

    &--two-color {
      --progress-stroke: #f52f6e;
    }

    &--three-color {
      --progress-stroke: #5a87fc;
    }

    &--four-color {
      --progress-stroke: #03cea4;
    }
  }

  &__value {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: bold;
    font-size: 32px;
    line-height: 1.5;
    color: var(--black);
  }

  &__text {
    display: block;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    color: var(--black);
  }

  @include small-tablet {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
