.how-we-do {
  padding-top: 175px;
  padding-bottom: 120px;

  &__title {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 30px;
    }
  }

  &__list {
    counter-reset: section;
  }

  &__item {
    position: relative;
    grid-column: 3 span;

    &::before {
      counter-increment: section;
      content: "0" counter(section);
      font-weight: bold;
      font-size: 46px;
      line-height: 1.3;
      color: #d7dadd;
    }

    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 30px;
        width: 75%;
        height: 1px;
        background-image: url("../img/how-we-do-line.svg");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        @include tablet {
          display: none;
        }
      }
    }

    &__subtitle {
      margin: 0;
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      color: var(--black);
      margin-top: 17px;
      margin-bottom: 8px;
    }

    &__descr {
      margin: 0;
      font-size: 16px;
      line-height: 1.6;
      color: var(--storm);
    }

    @include tablet {
      grid-column: 6 span;
    }
  }

  @include mobile {
    padding: 70px 0;
  }
}
