.learn-more {
  display: flex;
  align-items: center;
  justify-content: center;

  &__subtitle {
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    margin-right: 40px;

    @include small-tablet {
      max-width: 50%;
      margin-right: 10px;
    }

    @include mobile {
      max-width: none;
      margin-right: 0;
      margin-bottom: 10px;
    }
  }

  @include mobile {
    display: block;
  }
}
