.video-block {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  height: 500px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 3;
    background: #1e212c;
    opacity: 0.6;
    border-radius: 4px;
  }

  &--played {
    &::before {
      opacity: 0;
      visibility: hidden;
    }
  }

  &__content {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__play {
    background-color: var(--primary);
    border-radius: 100%;
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 10;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all 0.3s ease-in-out;

    & svg {
      width: 26px;
      height: 26px;
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--active-color);
    }

    &:active {
      background-color: var(--active-color);
    }

    &--played {
      opacity: 0;
      visibility: hidden;
    }
  }

  @include mobile {
    height: 300px;
  }
}
