.positions-content {
  padding-top: 120px;
  padding-bottom: 176px;

  &__left {
    grid-column: 7 span;
  }

  &__right {
    grid-column: 5 span;
    display: flex;
    justify-content: flex-end;
  }

  @include small-tablet {
    padding: 70px 0;
  }
}
.positions-list {
  &__item {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }
}
.positions-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 32px 40px;
  background: var(--white);
  border: 1px solid #e5e8ed;
  box-sizing: border-box;
  border-radius: 4px;

  &__left {
    @include tablet {
      margin-bottom: 20px;
    }
  }

  &__meta {
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
    margin-bottom: 8px;
  }

  &__city {
    position: relative;
    margin-right: 21px;
    padding-left: 23px;
    background-image: url("../img/city.svg");
    background-position: left center;
    background-repeat: no-repeat;
    background-size: 16px 16px;

    &::after {
      content: "";
      position: absolute;
      right: -13px;
      top: 50%;
      width: 1px;
      height: 11px;
      background-color: currentColor;
      transform: translateY(-50%);
    }
  }

  &__time {
    padding-left: 23px;
    background-image: url("../img/Clock.svg");
    background-position: left center;
    background-repeat: no-repeat;
    background-size: 16px 16px;
  }

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    color: var(--black);
  }

  &__link {
    display: block;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--primary);
    padding: 12px 31px;
    transition: all 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--active-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  @include tablet {
    flex-direction: column;
    align-items: start;
  }
}

.positions-looking {
  max-width: 388px;

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 24px;
  }

  &__descr {
    margin: 0;
    font-size: 18px;
    line-height: 1.5;
    color: var(--gray);
    margin-bottom: 48px;
  }

  &__btns {
    @media (max-width: 1024px) {
      display: flex;
      flex-direction: column;

      .positions-looking__btn {
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__btn {
    &--subs {
      padding-left: 44px;
      padding-right: 44px;
      transition: all 0.3s ease-in-out;

      &:focus {
        outline: 2px solid var(--primary);
        outline-offset: 2px;
      }

      &:hover {
        color: var(--active-color);
      }

      &:active {
        color: var(--active-color);
      }
    }

    &--send {
      padding-right: 54px;
      padding-left: 54px;
    }
  }
}
