.social {
  display: flex;
  align-items: center;

  &__item {
    margin-right: 16px;
    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    display: block;
    width: 24px;
    height: 24px;
    transition: all 0.3s ease-in-out;

    & svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      fill: var(--white);
      opacity: 0.6;
      transition: all 0.3s ease-in-out;
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      & svg {
        opacity: 1;
      }
    }

    &:active {
      & svg {
        opacity: 1;
      }
    }
  }
}
