.page-hero {
  padding-top: 152px;
  background-color: var(--athens);
  padding-bottom: 180px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right center;

  &__title {
    margin: 0;
    margin-bottom: 27px;
    max-width: 600px;

    &--sm {
      font-weight: bold;
      font-size: 64px;
      line-height: 1.2;
      letter-spacing: 1px;
      color: var(--black);
      text-transform: initial;
    }

    &--mb {
      margin-bottom: 60px;
    }

    @include mobile {
      font-size: 40px;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    svg {
      fill: var(--storm);
    }

    @include mobile {
      flex-direction: column;
      align-items: start;
    }
  }

  &__meta {
    .article-meta__item {
      &:first-child {
        color: var(--primary);
      }
      &:nth-child(2) {
        padding-left: 28px;
        background-image: url("../img/Clock.svg");
        background-position: left center;
        background-repeat: no-repeat;
        background-size: 20px 20px;
      }
      &:not(:last-child)::after {
        right: -17px;
        height: 16px;
      }
      &:not(:last-child) {
        margin-right: 25px;
      }
      .article-meta__item--comments {
        background-size: 20px 20px;
      }
      font-size: 16px;
      line-height: 1.6;
      color: var(--storm);
    }

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__desc {
    max-width: 600px;
    margin: 0;
    font-size: 20px;
    line-height: 1.5;
    color: var(--storm);

    @include mobile {
      font-size: 16px;
    }
  }

  &--services {
    @include image-set("../img/services-hero-bg");
  }

  &--services-inner {
    @include image-set("../img/services/services-inner-bg");
    padding-bottom: 115px;
  }

  &--work {
    @include image-set("../img/hero-work-bg");
  }

  &--work-inner {
    background-color: var(--white);
    @include image-set("../img/hero-work-inner-bg");
    padding-bottom: 110px;
    background-position: right -8px top 48px;
  }

  &--about {
    @include image-set("../img/hero-about-bg");
  }

  &--positions {
    background-position: right bottom;
    @include image-set("../img/hero-positions");
    padding-bottom: 143px;
    background-size: 1014px 506px;
  }

  &--news {
    @include image-set("../img/hero-news");
    padding-bottom: 237px;
  }

  &--post {
    padding-bottom: 150px;
    .page-hero__title {
      margin: 0;
      max-width: 100%;
      margin-bottom: 50px;

      @include small-tablet {
        font-size: 45px;
      }

      @include mobile {
        font-size: 35px;
      }
    }

    .social__link {
      svg {
        fill: var(--storm);
      }
    }
  }

  &--contacts {
    @include image-set("../img/hero-contacts");
    padding-bottom: 210px;
  }

  @include tablet {
    background-image: none;
    padding: 100px 0;
  }
}
