.slider-nav {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 15;
  &__btn {
    width: 48px;
    height: 48px;
    border-radius: 100%;
    color: var(--black);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    svg {
      display: block;
      width: 24px;
      height: 24px;
      fill: var(--black);
      object-fit: cover;
      color: inherit;
      fill: currentColor;
    }

    &:hover {
      background-color: var(--primary);

      svg {
        fill: var(--white);
      }
    }
  }
}

.slider-nav__btn.swiper-button-disabled {
  opacity: 0.4;
  pointer-events: none;
}
