.categories {
  padding-top: 120px;
  padding-bottom: 180px;

  @include mobile {
    padding: 40px 0;
  }
}
.categories-nav {
  margin-bottom: 60px;
  margin-top: 40px;
  display: flex;
  align-items: center;

  &__item {
    width: calc(100% / 6);

    @include tablet {
      width: calc(100% / 3);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__btn {
    padding: 10px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    text-align: center;
    color: #9a9ca5;
    transition: 0.3s color ease-in-out;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }

    &--active {
      border: 1px solid var(--primary);
      border-radius: 4px;
      color: var(--primary);
    }
  }

  @include tablet {
    flex-wrap: wrap;
  }
}
.categories-list {
  row-gap: 60px;
  column-gap: var(--grid-gap);
  margin-bottom: 55px;
  &__item {
    grid-column: 6 span;
  }

  .news-article__descr {
    overflow: hidden;
    height: 52px;
  }
}
