  &__link {
  }

  &__meta {
  }

  &__descr {
  }
}
.article-meta {
  &__item {
  }

  &__item--comments {
  }
}

.categories-pag {
  display: flex;
  align-items: center;
  justify-content: center;

  &__item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &__link {
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    transition: color 0.3s ease-in-out;

    &--active {
      color: var(--primary);
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }

    svg {
      display: block;
      width: 24px;
      height: 24px;
      object-fit: cover;
      fill: currentColor;
    }
  }
}
