.hero {
  padding-top: 84px;
  &__content {
    max-width: 580px;
  }

  &__next {
    position: absolute;
    top: 50%;
    right: 32px;
    transform: translateY(-50%);
    background-color: var(--white);
    z-index: 10;

    @include big-desktop {
      display: none;
    }
  }

  &__prev {
    position: absolute;
    top: 50%;
    left: 32px;
    transform: translateY(-50%);
    background-color: var(--white);
    z-index: 10;

    @include big-desktop {
      display: none;
    }
  }

  &__pag {
    counter-reset: pag;
    display: flex;
    left: calc((100% - 1260px) / 2) !important;
    bottom: 120px !important;

    .swiper-pagination-bullet {
      display: flex;
      flex-direction: column;
      text-align: left;
      border-radius: 0;
      width: 178px;
      height: auto;
      background-color: transparent;
      opacity: 1;

      &::before {
        counter-increment: pag;
        content: "0" counter(pag);
        font-weight: bold;
        font-size: 28px;
        line-height: 1.5;
        color: rgba(#fff, 0.3);
        transition: color 0.3s ease-in-out;
        margin-bottom: 10px;
      }

      &::after {
        display: none;
      }

      @include small-tablet {
        width: 25% !important;
      }
    }

    @include big-desktop {
      left: 3% !important;
    }

    @include mini-desktop {
      left: 15px !important;
    }

    @include small-tablet {
      bottom: 50px !important;
      width: calc(100% - 30px) !important;
    }

    @include mobile {
      display: none;
    }
  }

  &__bar {
    position: relative;
    overflow: hidden;
    border-radius: 100px;
    width: 100%;
    height: 2px;
    background-color: rgba(#fff, 0.3);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: var(--white);
      transform-origin: left;
      transform: scaleX(0);
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    margin-top: 63px;

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }

    & .btn {
      &:not(:last-child) {
        margin-right: 22px;

        @include mobile {
          margin-right: 0;
          margin-bottom: 20px;
        }
      }
    }
  }

  @include mobile {
    padding-top: 64px;
  }
}

.hero-slider {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hero-slide {
  @include image-set("../img/hero-bg");
  background-color: var(--gray);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 220px;
  padding-bottom: 356px;

  @include small-tablet {
    padding-top: 150px;
    padding-bottom: 200px;
  }

  @include mobile {
    padding: 100px 0;
  }
}

.swiper-pagination-bullet-active {
  &::before {
    color: var(--white) !important;
  }

  .hero__bar {
    &::after {
      transform: scaleX(1);
      transition: transform var(--hero-slider-speed) ease-in-out;
    }
  }
}
