.pricing {
  position: relative;
  padding: 82px 0 22px;
  background-color: var(--athens);

  &__decorate {
    position: absolute;
    left: 0;
    top: -8px;
  }
}
.table {
  border-collapse: collapse;
  width: 100%;

  @include small-tablet {
    width: 1200px;
  }

  &-wrapper {
    position: relative;
    padding: 32px 0 32px 24px;
    margin-top: 50px;
    background-color: var(--white);
    border: 1px solid #e5e8ed;
    width: 100%;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    border-radius: 4px;

    @include small-tablet {
      overflow-x: auto;
    }
  }

  &__row {
    &:last-child {
      .table__col {
        padding-top: 32px;
      }
    }

    @include tablet {
      td {
        margin-right: 10px;
      }
    }
  }

  &__th {
    padding-bottom: 24px;
    &:first-child {
      vertical-align: bottom;
      text-align: left;
      font-weight: bold;
      font-size: 18px;
      line-height: 1.5;
      color: var(--black);
    }
  }

  &__heading {
    font-weight: bold;
    font-size: 24px;
    line-height: 1.5;
    text-align: center;
    color: var(--black);
  }

  &__per {
    font-weight: bold;
    font-size: 18px;
    line-height: 1.5;
    text-align: center;
    color: var(--storm);
  }

  &__body {
    .table__row {
      &:nth-child(odd) {
        background-color: var(--athens);
      }
    }
  }

  &__row {
    &:last-child {
      width: 100%;
      .table__col {
        @include tablet {
          width: 25%;
        }
      }
    }
  }

  &__col {
    padding: 16px 0;
    vertical-align: middle;
    text-align: center;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);

    &:first-child {
      text-align-last: left;
    }

    .btn {
      padding: 10px 24px;
      font-size: 12px;

      &:focus {
        outline: 2px solid var(--primary);
        outline-offset: 2px;
      }
    }
  }
}
.tabel {
  &__check {
    font-size: 0;
    display: inline-block;
    width: 24px;
    height: 24px;
    background-image: url("../img/table-checked.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    vertical-align: middle;
  }

  &__text {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.6;
    text-align: center;
    color: var(--gray);
  }
}
