.breadcrumbs {
  display: flex;
  align-items: center;
  margin-bottom: 40px;

  &__item {
    &:not(:last-child) {
      margin-right: 22px;
      position: relative;

      &::after {
        content: "/";
        position: absolute;
        right: -14px;
        top: 0;
        color: #9a9ca5;
      }
    }
  }

  &__link {
    font-size: 14px;
    line-height: 1.5;
    color: var(--gray);
    transition: all 0.3s ease-in-out;

    &:not([href]) {
      color: #9a9ca5;
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }
  }

  @include small-tablet {
    flex-wrap: wrap;
  }
}
