.testimonials {
  background-color: #f4f5f7;
  padding-top: 180px;

  &__container {
    display: flex;
    justify-content: space-between;
  }

  &__content {
    max-width: 612px;

    @include tablet {
      max-width: 450px;
    }

    @include small-tablet {
      max-width: none;
      width: 100%;
    }
  }

  &__title {
    margin-bottom: 60px;
  }

  &__items {
    position: relative;
    padding-bottom: 12px;
  }

  &__img {
    position: relative;
    z-index: 5;
    margin-bottom: -60px;

    @include mini-desktop {
      width: 380px;
      height: 550px;

      & img {
        object-fit: cover;
        height: 100%;
      }
    }

    @include tablet {
      width: 300px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__slider-nav {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  @include small-tablet {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.testimonials-item {
  &__img {
    overflow: hidden;
    border-radius: 100%;
    width: 72px;
    height: 72px;
    margin-bottom: 25px;

    & img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__desc {
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    margin-bottom: 25px;
  }

  &__author {
    display: block;
    margin-bottom: 4px;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);
    padding-right: 110px;
  }

  &__position {
    display: block;
    padding-right: 110px;
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
  }
}
