.clients {
  padding-top: 180px;
  padding-bottom: 110px;
  background-color: var(--white);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 102px;

    @include mini-desktop {
      column-gap: 30px;
    }
  }

  &__item {
    @include mini-desktop {
      display: flex;
      justify-content: center;
      grid-column: 3 span;
    }
  }

  &__link {
    &:focus {
      outline: 2px solid var(--active-color);
      outline-offset: 2px;
    }
  }

  @include mobile {
    padding-bottom: 70px;
    padding-top: 70px;
  }
}
