.partners {
  padding-top: 80px;
  background-color: var(--athens);

  &__title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 102px;
    row-gap: 40px;

    @include mini-desktop {
      column-gap: 30px;
    }
  }

  &__item {
    @include mini-desktop {
      display: flex;
      justify-content: center;
      grid-column: 3 span;
    }
  }

  &--mb {
    padding-bottom: 120px;
  }

  &__link {
    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }
  }

  &--about {
    background-color: var(--white);
    padding-bottom: 174px;

    .partners__title {
      margin-bottom: 25px;
    }
  }

  @include mobile {
    padding-bottom: 80px;
    padding-top: 30px;
  }
}
