.contacts-section {
  @include image-set("../img/contacs-section-bg");
  padding: 80px 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: var(--black);

  &__container {
    display: flex;
    justify-content: flex-end;
  }

  &__form {
    background-color: var(--white);
  }

  @include mobile {
    padding: 15px;
  }
}
.contacts-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 495px;
  padding: 48px 40px;
  border: 1px solid #e5e8ed;
  box-shadow: 0px 60px 80px -20px rgba(30, 33, 44, 0.16), 0px 26px 24px -10px rgba(30, 33, 44, 0.1),
    0px 12px 10px -6px rgba(30, 33, 44, 0.08), 0px 4px 4px -4px rgba(30, 33, 44, 0.05);
  border-radius: 4px;

  &__title {
    margin: 0;
    width: 100%;
    margin-bottom: 24px;
    width: 100%;
    text-align: center;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    color: var(--black);
  }

  &__field {
    margin-bottom: 19px;
  }

  &__btn {
    font-size: 14px;
    letter-spacing: 0.5px;
    padding: 12px 31px;
  }

  @include mobile {
    padding: 15px 25px;
  }
}
