.post-form {
  padding-bottom: 180px;
  &__title {
    margin-bottom: 50px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__field {
    width: 48%;

    @include mobile {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__field--big {
    margin-top: 28px;
    width: 100%;

    .form-field__input {
      height: 100px;
    }

    @include mobile {
      margin-top: 0;
    }
  }

  &__btn {
    margin-top: 48px;
  }

  .form-field__caption {
    font-size: 16px;
    margin-bottom: 10px;
  }

  .form-field__input {
    font-size: 16px;
  }

  @include mobile {
    padding-bottom: 70px;
  }
}
