.team {
  padding-bottom: 164px;
  &__list {
    row-gap: 60px;
    margin-bottom: 80px;
  }

  &__item {
    grid-column: 3 span;
    text-align: center;

    &:hover {
      .team__soc {
        opacity: 1;
        visibility: visible;
      }
    }

    @include tablet {
      grid-column: 6 span;
    }
  }

  &__text {
    font-size: 18px;
    line-height: 1.5;
    text-align: center;
    color: var(--black);

    a {
      font-size: 18px;
      line-height: 1.5;
      text-align: center;
      text-decoration-line: underline;
      color: var(--primary);
      transition: all 0.3s ease-in-out;

      &:focus {
        outline: none;
        color: var(--primary);
      }

      &:hover {
        text-decoration: none;
        color: var(--active-color);
      }

      &:active {
        color: var(--active-color);
      }
    }
  }

  &__name {
    margin: 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 1.5;
    color: var(--gray);
  }

  &__post {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
  }

  &__top {
    position: relative;
    margin-bottom: 16px;
    font-size: 0;

    @include tablet {
      text-align: center;
    }
  }

  &__soc {
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 60px;
    background-color: rgba(#ff5a30, 0.7);
    border-radius: 0px 0px 4px 4px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }

  &__link {
    display: block;
    width: 20px;
    height: 20px;

    &:not(:last-child) {
      margin-right: 12px;
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      fill: var(--white);
    }
  }

  @include mobile {
    padding-bottom: 70px;
  }
}
