.history {
  background-color: var(--athens);
  padding-bottom: 100px;
  margin-bottom: 40px;

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 38px;
  }

  &__title {
  }

  &__slider-nav {
    transform: translate(10px, 10px);
  }

  &__prev {
  }

  &__next {
  }

  &__content {
    @include small-tablet {
      display: block;
    }
  }

  &__item {
    grid-column: 8 span;
    overflow: hidden;
  }

  &__image {
    margin-bottom: 36px;
  }

  &__descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
  }
}
.history-nav {
  position: relative;
  grid-column: 4 span;
  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__btn {
    position: relative;
    padding-left: 40px;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    color: #9a9ca5;
    transition: color 0.3s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      top: 13px;
      left: 3px;
      width: 1px;
      height: 16px;
      background-color: var(--athens);
    }

    &::after {
      content: "";
      position: absolute;
      top: 17px;
      left: 0px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: currentColor;
    }

    &:focus {
      outline: none;
      color: var(--primary);
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }

    &--active {
      color: var(--primary);
    }

    @include tablet {
      font-size: 22px;
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: 0px;
    left: 3px;
    width: 1px;
    height: 100%;
    background: rgba(#9a9ca5, 0.5);
  }

  @include small-tablet {
    margin-bottom: 30px;
  }
}
