.we-offer {
  padding-top: 120px;

  &__container {
    @include tablet {
      display: block;
    }
  }

  &__img {
    grid-column: 7 span;
  }

  &__content {
    grid-column: 5 span;
    padding-left: 76px;
    margin: 0;

    @include tablet {
      padding: 0;
      padding-top: 30px;
    }
  }

  &__title {
    margin: 0;
    font-family: "Lato";
    font-style: normal;
    margin-bottom: 60px;
  }
}

.tabs {
  &__tab {
    width: 100%;
    overflow: hidden;
    padding-left: 43px;
    position: relative;

    &:not(:first-child) {
      .tabs__tab-label {
        margin-top: 24px;

        &:not(:first-child) {
          &::before {
            top: 43px;
            left: 0;
          }

          &::after {
            top: 34px;
            left: 10px;
          }
        }
      }
    }
  }

  &__tab-label {
    display: block;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.5;
    color: var(--black);
    text-align: left;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
      color: var(--active-color);
    }

    &:active {
      color: var(--active-color);
    }

    &::before {
      content: "";
      position: absolute;
      top: 18px;
      left: 0;
      width: 21px;
      height: 2.5px;
      background-color: var(--primary);
      border-radius: 5px;
      transition: all 0.2s linear;
    }

    &::after {
      content: "";
      position: absolute;
      top: 10px;
      left: 10px;
      width: 2.5px;
      height: 21px;
      background-color: var(--primary);
      border-radius: 5px;
      opacity: 1;
      overflow: visible;
      transition: all 0.2s linear;
    }
  }

  &__tab-content {
    max-height: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
    margin-top: 12px;
    transition: all 0.2s;
  }

  input {
    position: absolute;
    opacity: 0;
    z-index: -1;

    &:checked {
      + .tabs__tab-label {
        margin-top: 0px;
        &::after {
          content: "";
          opacity: 0;
          overflow: hidden;
        }

        &:not(:first-child) {
          &::before {
            top: 18px;
            left: 0;
          }

          &::after {
            opacity: 0;
            overflow: hidden;
            top: 10px;
            left: 10px;
          }
        }
      }
      ~ .tabs__tab-content {
        max-height: 103px;
      }
    }
  }
}
