.services {
  position: relative;
  background-color: var(--white);
  padding-top: 116px;
  padding-bottom: 180px;
  overflow: hidden;

  &-decorate-1 {
    position: absolute;
    right: -8px;
    margin-top: -134px;
    top: 50%;
    transform: translateY(-50%);
  }

  &-decorate-2 {
    position: absolute;
    left: -6px;
    bottom: -148px;
  }

  &__list {
    position: relative;
    z-index: 5;
  }

  &__item {
    align-items: center;

    &:not(:last-child) {
      margin-bottom: 117px;
    }

    &--reverse {
      .services__info {
        order: -1;
        padding-left: 0;

        @include mobile {
          order: initial;
        }
      }

      .services__img {
        display: flex;
        justify-content: flex-end;
      }
    }
  }

  &__img {
    grid-column: 6 span;
  }

  &__info {
    grid-column: 6 span;
    padding-left: 27px;
    padding-bottom: 0;

    @include mobile {
      padding-left: 0;
    }
  }

  &__title {
    margin: 0;
    font-weight: bold;
    font-size: 32px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 24px;
  }

  &__descr {
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--storm);
    margin-bottom: 49px;
  }

  &__link {
    margin: 0;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--primary);
    width: 160px;
    padding: 13px 30px;
  }

  @include mobile {
    padding: 70px 0;
  }
}
