.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 84px;
  z-index: 1000;
  transition: all 0.3s ease-in-out;

  @include mobile {
    height: 64px;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 22px 15px;
    transition: all 0.3s ease-in-out;

    @include mobile {
      padding: 12px 15px;
    }
  }

  &.header__static {
    background-color: var(--athens);
    height: 64px;

    .header__inner {
      padding: 12px 15px;
    }

    .sidebar {
      top: 64px;
      background-color: var(--athens);
    }
  }

  &__nav {
    margin-left: -112px;

    @include mini-desktop {
      margin-left: 0;
    }
  }

  &--secondary {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
  }
}
.nav {
  @include tablet {
    display: none;
  }

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }

  &__link {
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    transition: color 0.3s;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--primary);
    }

    &:active {
      color: var(--active-color);
    }
  }
}
.header-contacts {
  display: flex;
  align-items: center;
  &__link {
    &:not(:last-child) {
      margin-right: 35px;
    }
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  @include tablet {
    display: none;
  }
}
.contacts-link {
  padding-left: 53px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 40px 40px;
  transition: all 0.3s ease-in-out;

  &:focus {
    outline: 2px solid var(--primary);
    outline-offset: 2px;
  }

  &:hover {
    .contacts-link__value {
      color: var(--active-color);
      text-decoration: underline;
    }
  }

  &:active {
    .contacts-link__value {
      color: var(--active-color);
    }
  }

  &__caption {
    font-weight: bold;
    font-size: 14px;
    line-height: 1.5;
    color: var(--gray);
  }

  &__value {
    font-size: 18px;
    line-height: 1.5;
    color: var(--black);
    transition: all 0.3s ease-in-out;

    @include mini-desktop {
      font-size: 16px;
    }
  }
}
.contacts-link--tel {
  background-image: url("../img/phone.svg");
}
.contacts-link--mail {
  background-image: url("../img/chat.svg");
}
.header--main {
  background-color: var(--white);
}
.logo {
  &:focus {
    outline: none;
  }

  @include mini-desktop {
    max-width: 100px;
  }

  @include tablet {
    max-width: none;
  }
}

.sidebar {
  display: none;
  position: fixed;
  top: 84px;
  left: -230px;
  z-index: 1000;
  width: 230px;
  height: 100vh;
  background-color: var(--white);
  transition: 0.3s ease;
  overflow: auto;
  padding-bottom: 100px;

  &._active {
    left: 0;
  }

  @include tablet {
    display: block;
  }

  .nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 10px;
    padding-bottom: 20px;
  }

  .nav__link {
    font-size: 35px;
    text-align: center;
    margin-bottom: 20px;
    margin-right: 0;
    text-decoration: none;
    color: var(--black);
    font-weight: 700;
    padding-left: 15px;
  }

  .header-contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 15px;
    row-gap: 20px;
  }

  .contacts-link__caption {
    color: var(--primary);
  }

  .contacts-link__value {
    color: var(--black);
  }

  @include mobile {
    top: 64px;
  }
}
