.form-field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  &__caption {
    font-size: 14px;
    line-height: 1.5;
    color: var(--gray);
    margin-bottom: 8px;
  }

  &__input {
    padding-left: 16px;
    width: 260px;
    height: 44px;
    background: var(--athens);
    border: 1px solid #d7dadd;
    border-radius: 4px;
    font-size: 14px;
    line-height: 1.5;

    &::placeholder {
      color: #9a9ca5;
    }

    &--big {
      padding-top: 10px;
      height: 66px;
      resize: none;
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }
  }

  &--big {
    flex-grow: 1;

    & input {
      width: 100%;
    }
  }

  &--gray {
    width: 100%;

    .form-field__input {
      width: 100%;
      background-color: var(--athens);
    }
  }
}
