.service-section {
  padding-top: 212px;
  background-image: url("../img/services-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 50px;

  &__list {
    margin-bottom: 45px;
  }

  &__learn {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
.services {
  &-list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--grid-gap);

    &__item {
      grid-column: 3 span;

      @include tablet {
        grid-column: 6 span;
      }

      @include mobile {
        grid-column: 4 span;
      }
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &-item {
    padding: 90px 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 4px;
    border: 1px solid #e5e8ed;
    box-shadow: 0px 80px 80px -20px rgba(154, 156, 165, 0.08), 0px 30px 24px -10px rgba(154, 156, 165, 0.05),
      0px 12px 10px -6px rgba(154, 156, 165, 0.04), 0px 4px 4px -4px rgba(30, 33, 44, 0.03);
    background: var(--white);

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 3;
      background-color: var(--white);
      border-radius: inherit;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 5;
      background-color: rgba(#1e212c, 0.6);
      border-radius: inherit;
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }

    &--construct {
      &::before {
        content: "";
        background-image: url("../img/services-1.jpg");
      }
    }

    &__content {
      position: relative;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    &__title {
      margin: 0;
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      text-align: center;
      color: var(--black);
      transition: color 0.3s ease-in-out;

      @include mini-desktop {
        font-size: 18px;
      }
    }

    &__icon {
      width: 52px;
      height: 52px;
      object-fit: cover;
      fill: var(--primary);
      margin-bottom: 24px;
      transition: fill 0.3s ease-in-out;
    }

    &:hover {
      &::before {
        opacity: 1;
      }

      &::after {
        opacity: 1;
      }

      .services-item__icon {
        fill: var(--white);
      }

      .services-item__title {
        color: var(--white);
      }
    }
  }
}
