.modal {
  position: relative;
  background-color: #fff;
  width: 100%;
  max-width: 486px;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.3s ease-in-out;
  display: none;
  border-radius: 20px;

  @media (max-width: 730px) {
    border-radius: 0;
  }

  &--visible {
    display: flex;
  }

  &-close {
    position: absolute;
    top: 24px;
    right: 24px;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }

    @media (max-width: 544px) {
      top: 20px;
      right: 20px;
    }
  }

  &__inner {
    width: 100%;
    padding: 48px;

    @include mobile {
      padding: 20px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 1.5;
    color: var(--black);
    margin-bottom: 24px;

    @include mobile {
    }
  }

  &-overlay {
    overflow: auto;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;

    &--visible {
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease-in-out;
    }
  }

  &__btn {
    display: block;
    width: 100%;
    margin-top: 20px;
  }

  &__field {
    margin-bottom: 20px;
  }
}

.modal-cv {
  align-self: start;
  overflow: auto;
}

.modal select:first-child {
  font-size: 14px;
  line-height: 1.5;
  color: #9a9ca5;
}
