.recent-news {
  background-color: var(--white);
  padding-bottom: 90px;

  &__list {
    background-color: var(--white);
    width: 100%;
    grid-template-rows: repeat(2, 255px);
    margin-bottom: 48px;

    @include tablet {
      grid-template-rows: initial;
    }
  }

  &__item {
    grid-column: 5 span;
    &:first-child {
      grid-column: 7 span;
      grid-row: 2 span;

      @include tablet {
        grid-column: 12 span;
        grid-row: auto;
      }

      @include mobile {
        grid-column: 4 span;
      }
    }

    @include tablet {
      grid-column: 12 span;
    }

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__title {
    margin-bottom: 60px;

    @include mobile {
      margin-bottom: 30px;
    }
  }
}
