.comment {
  display: flex;

  &__left {
    width: calc(100% - 630px);
    padding-right: 29px;

    @include tablet {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__author {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
    font-size: 18px;
    line-height: 1.5;
    color: var(--black);
  }

  &__time {
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
  }

  &__right {
    width: 630px;

    @include tablet {
      width: 100%;
      margin-bottom: 20px;
    }
  }

  &__body {
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);

    a {
      font-weight: bold;
      color: var(--primary);
      transition: all 0.3s ease-in-out;
    }

    * {
      margin: 0;
      margin-bottom: 16px;

      &:last-child {
        margin: 0;
      }
    }
  }

  &__reply {
    font-size: 14px;
    line-height: 1.5;
    color: var(--storm);
    transition: all 0.3s ease-in-out;

    svg {
      display: block;
      object-fit: cover;
      margin-top: 3px;
      width: 16px;
      height: 16px;
      fill: currentColor;
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      color: var(--active-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  @include tablet {
    flex-direction: column;
    align-items: start;
  }
}
