.contacts {
  padding-top: 120px;
  padding-bottom: 120px;

  &__content {
    display: flex;
    justify-content: space-between;

    @include tablet {
      display: block;
    }
  }

  &__img {
    flex-shrink: 0;
    margin-right: 20px;

    @include tablet {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }

  .choices {
    width: 100%;
  }

  .choices__list--dropdown .choices__item--selectable:after {
    display: none;
  }

  @include mobile {
    padding: 50px 0;
  }
}
.form-contacts {
  width: 600px;

  &__field {
    width: 47.5%;

    @include mobile {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__fields {
    border: 0;
    width: 47.5%;
    margin: 0;
    padding: 0;
  }

  &__line {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 23px;

    @include mobile {
      display: block;
    }
  }

  &__field--big {
    width: 100%;
    margin-bottom: 40px;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    @include mobile {
      flex-direction: column;
      align-items: start;
    }
  }

  &__checkbox {
    max-width: 315px;

    .custom-checkbox__text {
      font-weight: normal;
      font-size: 16px;
      line-height: 1.6;
      color: var(--gray);

      &::before {
        content: "";
        top: 6px;
      }

      &::after {
        content: "";
        top: 6px;
      }
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  @include tablet {
    width: 100%;
  }
}
.form-field {
  &__caption {
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    margin-bottom: 8px;
  }

  &__input {
    height: 52px;
    font-size: 16px;
    line-height: 1.6;
    color: #9a9ca5;
  }

  &__select {
    display: block;
    width: 100%;
    background: none;
    border: none;
    appearance: none;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    background: var(--athens);
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding: 13px 0;
    padding-left: 16px;
    padding-right: auto;

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &--wrapper {
      width: 100%;
      position: relative;

      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        top: 50%;
        right: 16px;
        z-index: 1;
        background-image: url("../img/Down-chevron.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        transform: translateY(-50%);
      }
    }
  }

  &__flex {
    display: flex;
    align-items: center;
    height: 19px;
    margin-top: 5px;
  }
}
.custom-radio {
  cursor: pointer;
  position: relative;

  &:not(:last-child) {
    margin-right: 24px;
  }
  &__input {
    appearance: none;

    &:checked + .custom-radio__text::before {
      border-color: var(--primary);
    }

    &:checked + .custom-radio__text::after {
      opacity: 1;
    }
  }

  &__text {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0;
    font-size: 16px;
    line-height: 1.6;
    color: var(--gray);
    padding-left: 28px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0px;
      z-index: 1;
      display: block;
      width: 16px;
      height: 16px;
      border: 1px solid #b3b7bc;
      border-radius: 100%;
      transform: translateY(-50%);
      transition: border-color 0.3s linear;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 4px;
      z-index: 1;
      display: block;
      width: 8px;
      height: 8px;
      background-color: var(--primary);
      border-radius: 100%;
      transform: translateY(-50%);
      opacity: 0;
      transition: opacity 0.3s linear;
    }
  }
}

.form-field .form-field__input--big {
  height: 128px;
}
