html {
  box-sizing: border-box;
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-family);
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;

  &._lock {
    overflow: hidden;
  }
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; //     position: sticky -   
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  max-width: 1260px;
  padding: 0 15px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.main-title {
  margin: 0;
  margin-bottom: 24px;
  font-family: "Ubuntu";
  font-weight: bold;
  font-size: 72px;
  line-height: 1.3;
  letter-spacing: 1px;
  color: var(--black);
  text-transform: uppercase;

  &--home {
    color: var(--white);
  }

  & span {
    color: var(--primary);
  }

  @include small-tablet {
    font-size: 60px;
  }

  @include mobile {
    font-size: 35px;
  }
}

.main-desc {
  margin: 0;
  font-family: "Ubuntu";
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.5;
  color: var(--storm);

  &--home {
    color: var(--white);
  }

  @include mobile {
    font-size: 16px;
  }
}

.btn {
  display: inline-block;
  font-weight: bold;
  font-size: 16px;
  padding: 15px 39px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  border-style: solid;
  border-width: 1px;
  border-color: transparent;
  border-radius: 4px;
  color: var(--white);
  transition: all ease-in-out 0.3s;

  &--stroke {
    &-light {
      border-color: var(--white);
      color: var(--white);
    }

    &-primary {
      border-color: var(--primary);
      color: var(--primary);
    }

    &:focus {
      outline: 2px solid var(--primary);
      outline-offset: 2px;
    }

    &:hover {
      border-color: var(--primary);
      background-color: var(--primary);
    }

    &:active {
      border-color: var(--primary);
      background-color: var(--active-color);
    }
  }

  &--fill {
    background-color: var(--primary);
    transition: all ease-in-out 0.3s;

    &:focus {
      outline: 2px solid var(--active-color);
      outline-offset: 2px;
    }

    &:hover {
      background-color: var(--active-color);
    }

    &:active {
      background-color: var(--active-color);
    }
  }
}

.title {
  margin: 0;
  font-weight: bold;
  font-size: 46px;
  line-height: 1.3;
  margin-bottom: 23px;
  color: var(--black);

  &--center {
    text-align: center;
  }

  &--sm {
    font-size: 28px;
    line-height: 1.5;
    margin-bottom: 32px;
  }

  &--white {
    color: var(--white);
  }

  @include mobile {
    font-size: 28px;
  }
}

.descr {
  margin: 0;
  font-size: 18px;
  line-height: 1.5;
  color: var(--storm);
  margin-bottom: 59px;

  &--center {
    text-align: center;
  }

  @include mobile {
    font-size: 16px;
    margin-bottom: 25px;
  }
}

.section-offset {
  padding-top: 120px;

  @include small-tablet {
    padding-top: 60px;
  }

  @include mobile {
    padding-top: 40px;
  }
}

.svg-video-block {
  fill: #ffffff;
}

.centered {
  display: flex;
  justify-content: center;
  width: 100%;
}

.custom-checkbox {
  margin-bottom: 23px;

  &__input {
    position: absolute;
    opacity: 0;
  }

  &__text {
    position: relative;
    display: inline-block;
    padding-left: 27px;
    font-size: 14px;
    line-height: 150%;
    color: var(--gray);
  }

  &__text::before {
    content: "";
    position: absolute;
    left: -1px;
    top: 3px;
    display: inline-block;
    border: 1px solid var(--primary);
    border-radius: 3px;
    width: 16px;
    height: 16px;
    transition: background-color 0.3s ease-in-out;
  }

  &__text::after {
    content: "";
    position: absolute;
    left: -1px;
    top: 3px;
    width: 16px;
    height: 16px;
    background-image: url("img/check.svg");
    background-position: center;
    background-size: 10px 8px;
    background-repeat: no-repeat;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
  }

  &__input:checked + &__text::before {
    background-color: var(--primary);
  }

  &__input:checked + &__text::after {
    opacity: 1;
  }

  &__input:focus + &__text {
    outline: 3px solid var(--primary);
  }
}

.custom-file {
  position: relative;
  display: flex;
  justify-content: flex-start;
  margin-top: 4px;
  margin-bottom: 20px;
  cursor: pointer;

  &__input {
    width: 100%;
    height: 100%;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  &__text {
    display: inline-block;
    padding-left: 36px;
    font-size: 14px;
    line-height: 160%;
    color: var(--gray);
    background-image: url("img/Attach.svg");
    background-repeat: no-repeat;
    background-position: left center;
    background-size: 24px 24px;
  }

  &__input:focus + &__text {
    outline: 3px solid var(--primary);
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);

  @include mobile {
    grid-template-columns: repeat(4, 1fr);
  }
}

.burger {
  display: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  padding: 0;
  border: none;

  @include tablet {
    display: block;
  }
}
.burger__line {
  display: block;
  position: relative;
  width: 100%;
  height: 3px;
  background-color: var(--black);
}
.burger__line::before,
.burger__line::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 3px;
  left: 0;
  background-color: var(--black);
  z-index: 1;
  transition: all 0.2s;
}
.burger__line::before {
  bottom: 12px;
}
.burger__line::after {
  bottom: -12px;
}
.burger-active .burger__line {
  background-color: transparent;
}

.burger-active .burger__line::before {
  top: 0;
  transform: rotate(-45deg);
}

.burger-active .burger__line::after {
  top: 0;
  transform: rotate(45deg);
}
