.post {
  &__img {
    display: block;
    width: 100%;
    object-fit: cover;
    margin-top: -72px;
  }

  &__wrapper {
    margin: 0 auto;
    max-width: 838px;
    padding: 0 15px;
  }

  &__share {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.6;
    color: var(--black);

    .social {
      margin-left: 15px;

      &__item {
        &:not(:last-child) {
          margin-right: 11px;
        }
      }

      &__link {
        svg {
          fill: var(--storm);
        }
      }
    }
  }

  &__content {
    padding-top: 65px;
    padding-bottom: 25px;
    * {
      margin-bottom: 28px;
      font-size: 16px;
      line-height: 1.6;
      color: var(--gray);
    }

    h2 {
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      color: var(--black);
      margin-bottom: 25px;
    }

    h3 {
      font-weight: bold;
      font-size: 18px;
      line-height: 1.5;
      color: var(--black);
      margin-bottom: 25px;
    }

    p {
      margin: 0;
      font-size: 16px;
      line-height: 1.6;
      color: var(--gray);
      margin-bottom: 28px;
    }

    blockquote {
      position: relative;
      margin: 0;
      margin-bottom: 28px;
      padding: 20px 58px;
      padding-right: 5px;
      font-weight: bold;
      font-size: 20px;
      line-height: 1.5;
      color: var(--black);

      &::before {
        content: "";
        position: absolute;
        left: 0px;
        top: 21px;
        width: 28px;
        height: 20px;
        background-image: url("../img/braces.svg");
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: cover;
      }

      @include mobile {
        padding: 0px;
        padding-top: 50px;
      }
    }

    ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
      margin-bottom: 24px;

      li {
        font-size: 16px;
        line-height: 1.6;
        color: var(--gray);
        padding-left: 32px;
        position: relative;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 16px;
          height: 16px;
          background-image: url("../img/check-list.svg");
          background-repeat: no-repeat;
          background-size: cover;
          background-position: center;
        }

        &:not(:last-child) {
          margin-bottom: 4px;
        }
      }
    }

    @include mobile {
      padding-top: 30px;
    }
  }
}
